@mixin default-font($deffont) {
  font-family: Roboto;
  font-size: $deffont; }

a {
  text-decoration: none; }

h1 {
  @include default-font(35px);
  line-height: 1.166666667;
  font-weight: bold;
  color: rgb(51, 51, 51);
  margin: 23px 0 0 0; }

h2 {
  color: rgb(51, 51, 51);
  @include default-font(24px);
  font-weight: bold;
  line-height: 33.6px;
  margin-bottom: 24px;
  margin-top: 24px; }


h3 {
  @include default-font(1rem);
  color: rgb(102, 102, 102);
  font-weight: 400;
  margin-top: 18px;
  margin-bottom: .9375rem; }

h4 {
  color: rgb(44, 126, 240);
  @include default-font(18px);
  line-height: 24px;
  font-weight: 400;
  margin-bottom: 8px; }

h4:hover {
  color: rgb(21, 82, 168); }


.bold {
  font-weight: 700; }


.sponsored {
  background-color: rgb(235, 235, 235);
  color: rgb(51, 51, 51);
  @include default-font(10px);
  font-weight: 500;
  vertical-align: middle;
  margin-right: .25rem;
  line-height: 1;
  padding: .25rem .5rem .25rem;
  height: 20px;
  margin-top: 10px; }

.sponsored-by img {
  height: 40px; }

.sponsored-by {
  display: flex;
  margin-bottom: 4px; }



